import { HttpClient, HttpParams } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Router } from "@angular/router";
import { BehaviorSubject, Observable, firstValueFrom, tap } from "rxjs";
import { environment } from "src/environments/environment";

@Injectable({
  providedIn: "root",
})
export class CheckoutLandService {

  private url: string = `${environment.urlWeb}`;
  private _userInfo: BehaviorSubject<any[] | null> = new BehaviorSubject(null);
  private _processing: BehaviorSubject<any[] | null> = new BehaviorSubject(null);
  private _pulserasTypes: BehaviorSubject<any[] | null> = new BehaviorSubject(null);

  //user data
  private _userData: BehaviorSubject<any[] | null> = new BehaviorSubject(null);
  private _addressData: BehaviorSubject<any[] | null> = new BehaviorSubject(null);
  private _pulserasData: BehaviorSubject<any[] | null> = new BehaviorSubject(null);

  private _idtransaccion: BehaviorSubject<any[] | null> = new BehaviorSubject(null);

  //completed data
  private _userOk: BehaviorSubject<any[] | null> = new BehaviorSubject(null);
  private _pulseraOk: BehaviorSubject<any[] | null> = new BehaviorSubject(null);

  private _checkoutOk: BehaviorSubject<any[] | null> = new BehaviorSubject(null);

  private _metodoPago: BehaviorSubject<any[] | null> = new BehaviorSubject(null);

  //checkout-exito
  private _order: BehaviorSubject<any[] | null> = new BehaviorSubject(null);
  /**
   * Constructor
   */
  constructor(private _httpClient: HttpClient, private _router: Router) {}

  /**
   * Getter for userInfo
   */
  get userInfo$(): Observable<any[]> {
    return this._userInfo.asObservable();
  }

  /**
   * Setter for userInfo
   */
  setUserInfo(value:any){
    this._userInfo.next(value);
  }

  /**a
   * Getter for processing
   */
  get processing$(): Observable<any[]> {
    return this._processing.asObservable();
  }

  /**
   * Setter for processing
   */
  setProcessing(value:any){
    this._processing.next(value);
  }

  /**a
   * Getter for userData
   */
  get userData$(): Observable<any[]> {
    return this._userData.asObservable();
  }

  /**
   * Setter for userData
   */
  setUserData(value:any){
    this._userData.next(value);
  }

  /**a
   * Getter for addressData
   */
  get addressData$(): Observable<any[]> {
    return this._addressData.asObservable();
  }

  /**
   * Setter for addressData
   */
  setAddressData(value:any){
    this._addressData.next(value);
  }

  /**a
   * Getter for pulserasData
   */
  get pulserasData$(): Observable<any[]> {
    return this._pulserasData.asObservable();
  }

  /**
   * Setter for pulserasData
   */
  setPulserasData(value:any){
    this._pulserasData.next(value);
  }

  /**a
   * Getter for userOk
   */
  get userOk$(): Observable<any[]> {
    return this._userOk.asObservable();
  }

  /**
   * Setter for userOk
   */
  setuserOk(value:any){
    this._userOk.next(value);
  }

  /**a
   * Getter for pulseraOk
   */
  get pulseraOk$(): Observable<any[]> {
    return this._pulseraOk.asObservable();
  }

  /**
   * Setter for pulseraOk
   */
  setpulseraOk(value:any){
    this._pulseraOk.next(value);
  }

  /**a
   * Getter for checkoutOk
   */
  get checkoutOk$(): Observable<any[]> {
    return this._checkoutOk.asObservable();
  }

  /**
   * Setter for checkoutOk
   */
  setcheckoutOk(value:any){
    this._checkoutOk.next(value);
  }

  /**a
   * Getter for metodoPago
   */
  get metodoPago$(): Observable<any[]> {
    return this._metodoPago.asObservable();
  }

  /**
   * Setter for metodoPago
   */
  setmetodoPago(value:any){
    this._metodoPago.next(value);
  }

  /**
     * Getter for countries
     */
  get pulserasTypes$(): Observable<any[]> {
    return this._pulserasTypes.asObservable();
  }

  /**a
   * Getter for checkoutOk
   */
  get order$(): Observable<any[]> {
    return this._order.asObservable();
  }

  

  getFieldsByUserTypes(type: any): Observable<any> {
    let params = new HttpParams();
    params = params.set("type", type);
    return this._httpClient.get<any[]>(`${this.url}authentication/fields`, {
      params,
    });
  }

  getUserTypes(): Observable<any>{
    return this._httpClient.get<any[]>(`${this.url}tipos`).pipe(
        tap((response: any) => {
            this._pulserasTypes.next(response);
        })
    );
  }

  generateOrder(data: any): Observable<any> {
    return this._httpClient.post(`${this.url}pagos/crearIntento`, data);
  }

  sendPayment(token: string, id: string): Promise<any> {
    return firstValueFrom(
      this._httpClient.patch(`${this.url}pagos/generarMetodo/${id}`, {
        token: token,
      })
    );
  }

  getOrder(id: any): Observable<any> {
    return this._httpClient.get(`${this.url}pagos/obtenerIntento/${id}`).pipe(
      tap((response: any) => {
          this._order.next(response);
      }
      )
    );
  }

  confirmPayment(id: string): Promise<any> {
    return firstValueFrom(
      this._httpClient.patch(`${this.url}pagos/confirmar/${id}`, {})
    );
  }

  setAddessPulsera(data:any, id:any){
    return this._httpClient.post(`${this.url}manillas/update/${id}`, data);
  }

  setIdTransaccion(id:any): any{
    this._idtransaccion.next(id);
  }

  /**
   * Getter for idTransaccion
   */
  get idTransaccion$(): Observable<any[]> {
    return this._idtransaccion.asObservable();
  }
}
