import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Subject, takeUntil } from 'rxjs';
import { CheckoutLandService } from '../checkout-land.service';
import Swal from 'sweetalert2';
import { environment } from 'src/environments/environment';
import { FormBuilder, FormGroup } from '@angular/forms';
import { AuthService } from 'src/app/core/auth/auth.service';
import { UserService } from 'src/app/core/user/user.service';

declare global {
  interface Window {
    Stripe?: any;
  }
}

@Component({
  selector: 'app-checkout-cash',
  templateUrl: './checkout-cash.component.html',
  styleUrls: ['./checkout-cash.component.scss']
})
export class CheckoutCashComponent implements OnInit {

  loading:boolean = false;
  private readonly STRIPE!: any; //TODO: window.Stripe
  private elementStripe!: any;
  id!: string;
  orderData!: any;
  Toast:any;

  address: any;

  private _unsubscribeAll: Subject<any> = new Subject<any>();

  
  showDatosEnvio: boolean = true;
  
  //datos to create
  user:any = {};
  pulseras:any = [];

  paymentInfo:any = {};

  pulseraOk:any = '';
  userOk: any = '';

  checkOk: any = '';

  //usercreated
  userCreated: any = {};
  pulserasCreated: any = [];

  datosEnvio: any;

  metodoPago:any = '';

  constructor(
    private cd: ChangeDetectorRef, private route: ActivatedRoute,
    private _checkoutService: CheckoutLandService, private fb: FormBuilder,
    private _authService: AuthService, private _userService: UserService,
    private _router: Router
  ) { 
    this.STRIPE = window.Stripe(environment.stripe_pk);
    this.Toast = Swal.mixin({
      toast: true,
      position: 'top-end',
      showConfirmButton: false,
      timer: 3000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener('mouseenter', Swal.stopTimer)
        toast.addEventListener('mouseleave', Swal.resumeTimer)
      }
    });
  }

  ngOnInit() {
    // this.form = this.fb.group({
    //   amount: [''],
    // })
    this.createStripeElement();

    this._checkoutService.userData$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe((user: any) => {
        // Update the selected agente
        this.user = user;

        // Mark for check
        this.cd.markForCheck();
      });

    this._checkoutService.pulserasData$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe((pulseras: any) => {
        // Update the selected agente
        this.pulseras = pulseras;

        // Mark for check
        this.cd.markForCheck();
      });

    this._checkoutService.userOk$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe((userOk: any) => {
        // Update the selected agente
        this.userOk = userOk;
        if(this.userOk === 'ok' && this.pulseraOk === 'ok' && this.checkOk !== 'ok'){
          this._checkoutService.setcheckoutOk('ok');
        }else{
          this.enableInputs();
          // this.form.enable();
        }

        // Mark for check
        this.cd.markForCheck();
      });

    this._checkoutService.pulseraOk$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe((pulseraOk: any) => {
        // Update the selected agente
        this.pulseraOk = pulseraOk;
        if(this.userOk === 'ok' && this.pulseraOk === 'ok' && this.checkOk !== 'ok'){
          this._checkoutService.setcheckoutOk('ok');
        }else{
          this.enableInputs();
          // this.form.enable();
        }

        // Mark for check
        this.cd.markForCheck();
      });


    this._checkoutService.checkoutOk$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe((checkOk: any) => {
        this.checkOk = checkOk;
        if(checkOk === 'ok' && this.metodoPago === 'efectivo'){
          this.createUser();
        }
        // Mark for check
        this.cd.markForCheck();
      });

      this._checkoutService.metodoPago$
      .pipe(takeUntil(this._unsubscribeAll))
        .subscribe((metodo: any) => {
          this.metodoPago = metodo;
          // Mark for check
          this.cd.markForCheck();
        });
  }

  private createStripeElement = () => {
    //TODO: SDK de Stripe inicia la generacion de elementos
    this.elementStripe = this.STRIPE.elements({
      fonts: [
        {
          cssSrc:
            'https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400&display=swap',
        },
      ],
    });

    const address = this.elementStripe.create('address', {
      mode: 'shipping', allowedCountries:['MX'],
      fields: {
        phone: 'always',
      },
      validation: {
        phone: {
          required: 'always',
        },
      },
    });


    //TODO: SDK Montamos los elementos en nuestros DIV identificados on el #id
    address.mount('#datos-envio');
    this.address = address;
  }

  async initPay(){
    let address = this.elementStripe.getElement('address');
    const {complete, value} = await address.getValue();
    if(complete){
      this.datosEnvio = value;
      this.orderDataCliente(value);
    }
  }

  orderDataCliente(address:any){
    this._checkoutService.setProcessing('loading');
    this._checkoutService.setAddressData(address);
  }

  disableInputs(){
    this.loading = true;
  }

  enableInputs(){
    this.loading = false;
  }

  async executePayment(id:any): Promise<any>{
    var redirectURL:any;
    redirectURL = `/checkout-exito/${id}`;
    // Navigate to the redirect url
    this._router.navigateByUrl(redirectURL);
  }

  //Creacion usuario
  createUser(){
    setTimeout(() => {
      this.disableInputs();
      // this.form.disable();

      // console.log(this.user);
      this.verificarUser();
    }, 200);
  }

  /**
   * Sign up
   */
  signUp(): void {

    // Sign up
    this._authService.signUp(this.user).subscribe(
      (response) => {
        this.userCreated = response;
        let data = {
          email: this.user.email,
          password: this.user.password
        }

        this.signIn(data);
      },
      (response) => {
        // Re-enable the form
        this._checkoutService.setuserOk('error');
        this._checkoutService.setcheckoutOk('error');
        this._checkoutService.setpulseraOk('error');
        let message:any = response.error.message;
        // Set the alert

        this.Toast.fire({
          icon: 'error',
          title: message
        });
        // this.disableButton = true;
      }
    );
  }

  signIn(data:any): void{
    // Sign in
    this._authService.signIn(data).pipe(takeUntil(this._unsubscribeAll))
    .subscribe(
      (response) => {
        this.userCreated = response.user;
        this.ciclarPulseras(response.user);
      },
      (response) => {
        // Re-enable the form
        this._checkoutService.setuserOk('error');
        this._checkoutService.setcheckoutOk('error');
        this._checkoutService.setpulseraOk('error');
        let message:any = response.error.message;
        // Set the alert

        this.Toast.fire({
          icon: 'error',
          title: message
        });
      }
    );
  }

  verificarUser(){
    this._authService.existUser(this.user.email).pipe(takeUntil(this._unsubscribeAll)).subscribe(
      (response:any) => {
        if(response){
          let data = {
            email: this.user.email,
            password: this.user.password
          }
  
          this.signIn(data);
        }else{
          this.signUp();
        }
      },(error) => {
    
      }
    );
  }

  ciclarPulseras(user:any){
    this.pulseras.pulseras.forEach(element => {
      element.userId = user._id;
      element.otros = this.datosEnvio;
    });

    this.crearPulseras(this.pulseras.pulseras);
  }

  crearPulseras(data:any){
    this._userService.crearVariasManilla({manillas: data}).subscribe(
      (response) => {
        this.pulserasCreated = response;
        this.createPaymentIntent();
      },
      (response) => {
        this.Toast.fire({
          icon: 'error',
          title: response.error.message
        });
      }
    );
  }

  createPaymentIntent():any{
    let data = {
      userId: this.userCreated._id,
      monto: this.pulseras.precio,
      metodo: 'Efectivo',
      manillasId: this.getPulserasIds(),
      otros:{
        list: this.pulseras.lista,
        precio: this.pulseras.precio,
        envio: this.datosEnvio
      }
    }

    this._checkoutService.generateOrder(data).pipe(takeUntil(this._unsubscribeAll)).subscribe(
      (response:any) => {
        this.paymentInfo = response;
        this.executePayment(response._id);
      },(error) => {
        this.Toast.fire({
          icon: 'error',
          title: error.error.message
        });
      }
    );
  }

  getPulserasIds(){
    let pulseras: any = [];
    this.pulserasCreated.forEach(pulsera => {
      pulseras.push(pulsera._id);
    });

    return pulseras;
  }
}
