import { ChangeDetectorRef, Component, OnDestroy, OnInit } from '@angular/core';
import { CheckoutLandService } from '../checkout-land/checkout-land.service';
import { FormBuilder } from '@angular/forms';
import { ViewportScroller } from '@angular/common';
import { ActivatedRoute, Router } from '@angular/router';
import { Subject, takeUntil } from 'rxjs';

@Component({
  selector: 'app-checkout-exito',
  templateUrl: './checkout-exito.component.html',
  styleUrls: ['./checkout-exito.component.scss']
})
export class CheckoutExitoComponent implements OnInit, OnDestroy {
  private _unsubscribeAll: Subject<any> = new Subject<any>();

  // Logo
  public logo = [
    {
      image: "assets/images/logos/1.png",
    },
    {
      image: "assets/images/logos/3.png",
    },
    {
      image: "assets/images/logos/2.png",
    },
    {
      image: "assets/images/logos/4.png",
    },
    {
      image: "assets/images/logos/5.png",
    },
    {
      image: "assets/images/logos/6.png",
    },
    {
      image: "assets/images/logos/7.png",
    },
  ];

  pulseras: any = {};

  types: any = [];
  
  typesSelected: any = [];

  precio: any = 0;

  idPago: any = '';

  tod:any = new Date();

  orden: any = {};

  datosEnvio: any = {};
  

  fechaOrden: any = '';

  constructor(
    private _checkoutService: CheckoutLandService,
    private _formBuilder: FormBuilder,
    private _changeDetectorRef: ChangeDetectorRef,
    private _router: Router,
    private viewScroller: ViewportScroller,
    private _activatedRoute: ActivatedRoute,
  ) { }

  ngOnInit() {

    this._checkoutService.pulserasTypes$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe((types: any) => {
        this.types = types;
        // Mark for check
        this._changeDetectorRef.markForCheck();
      });


    this._checkoutService.order$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe((order: any) => {
        this.orden = order;
        this.idPago = order._id;
        this.precio = order?.otros?.precio || 0;
        this.typesSelected = order?.otros?.list || [];
        this.fechaOrden = order.createdAt;
        this.datosEnvio = order?.otros?.envio || {};
        // Mark for check
        this._changeDetectorRef.markForCheck();
      });
  }

  
  /**
   * On destroy
   */
  ngOnDestroy(): void {
    // Unsubscribe from all subscriptions
    this._unsubscribeAll.next(null);
    this._unsubscribeAll.complete();
  }

}
