import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { UserInfoComponent } from './user-info/user-info.component';
import { ObtenerIntentoPagoResolver, PulserasTypesResolver, UserInfoResolver } from './landing.resolver';
import { InicioComponent } from './inicio/inicio.component';
import { ContactComponent } from './contact/contact.component';
import { CheckoutLandComponent } from './checkout-land/checkout-land.component';
import { CheckoutExitoComponent } from './checkout-exito/checkout-exito.component';

export const routes: Routes = [
  { 
    path: 'u/:id', 
    component: UserInfoComponent ,
    resolve: {
      manilla: UserInfoResolver
    }
  },
  { 
    path: 'inicio', 
    component: InicioComponent,
  },
  { 
    path: 'contact', 
    component: ContactComponent,
  },
  { 
    path: 'checkout/:pulsera', 
    component: CheckoutLandComponent,
    resolve: {
      types: PulserasTypesResolver
    }
  },
  { 
    path: 'checkout-exito/:id', 
    component: CheckoutExitoComponent,
    resolve: {
      types: PulserasTypesResolver,
      order: ObtenerIntentoPagoResolver
    }
  },
]

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
  })
  export class LandingRoutingModule { }