import { ChangeDetectorRef, Component, OnInit, ViewChild } from '@angular/core';
import { AbstractControl, FormBuilder, FormGroup, NgForm, ValidationErrors, ValidatorFn, Validators } from '@angular/forms';
import { CheckoutLandService } from '../checkout-land.service';
import { AuthService } from 'src/app/core/auth/auth.service';
import { Router } from '@angular/router';
import { ViewportScroller } from '@angular/common';
import { Subject, takeUntil } from 'rxjs';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-register-card',
  templateUrl: './register-card.component.html',
  styleUrls: ['./register-card.component.scss']
})
export class RegisterCardComponent implements OnInit {

  //registro
  @ViewChild("signUpNgForm") signUpNgForm: NgForm;

  signUpForm: FormGroup;

  showAlert: boolean = false;
  messageAlert: string = "";

  private _unsubscribeAll: Subject<any> = new Subject<any>();

  loadingFields: boolean = false;

  fieldsList: any = [];

  maxDate: any = {};

  disableButton: boolean = true;

  imgUpload: any;

  uploadedFiles: any;

  imageBase64: any = "";

  Toast: any;

  activarClases: boolean = false;

  processing:any = '';

  address:any = {};

  continuePayment: any = '';

  constructor(
    private _checkoutService: CheckoutLandService,
    private _formBuilder: FormBuilder,
    private _authService: AuthService,
    private _changeDetectorRef: ChangeDetectorRef,
    private _router: Router,
    private viewScroller: ViewportScroller
  ) { 
    let today = new Date();
    this.maxDate.year = today.getFullYear();
    this.maxDate.month = today.getMonth() + 1;
    this.maxDate.day = today.getDate();
    this.Toast = Swal.mixin({
      toast: true,
      position: "top-end",
      showConfirmButton: false,
      timer: 3000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener("mouseenter", Swal.stopTimer);
        toast.addEventListener("mouseleave", Swal.resumeTimer);
      },
    });
  }

  static patternValidator(regex: RegExp, error: ValidationErrors): ValidatorFn {
    return (control: AbstractControl): { [key: string]: any } => {
      if (!control.value) {
        // if control is empty return no error
        return null;
      }

      // test the value of the control against the regexp supplied
      const valid = regex.test(control.value);

      // if true, return no error (no error), else return error passed in the second parameter
      return valid ? null : error;
    };
  }

  ngOnInit() {

    this.signUpForm = this._formBuilder.group({
      // name: ["", [Validators.required]],
      email: ["", [Validators.required, Validators.email]],
      password: [
        "",
        Validators.compose([
          Validators.required,
          RegisterCardComponent.patternValidator(/\d/, { hasNumber: true }),
          RegisterCardComponent.patternValidator(/[A-Z]/, { hasCapitalCase: true }),
          RegisterCardComponent.patternValidator(/[a-z]/, { hasSmallCase: true }),
          RegisterCardComponent.patternValidator(
            /[!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?]/,
            { hasSpecialCharacters: true }
          ),
          Validators.minLength(8),
        ]),
      ],
      // direccion: ["", [Validators.required]],
      // type: ["", [Validators.required]],
      // telefono: ["", [Validators.required, Validators.pattern("[0-9]*")]],
      // documento: ["", [Validators.required]],
      // fecha_nacimiento: ["", [Validators.required]],
      // clienteCampos: this._formBuilder.array([]),
    });

    this._checkoutService.processing$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe((processing: any) => {
        // Update the selected agente
        this.processing = processing;
        // Mark for check
        this._changeDetectorRef.markForCheck();
      });

    this._checkoutService.addressData$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe((address: any) => {
        // Update the selected agente
        this.address = address;
        if(this.processing === 'loading' && address.name){
          this.orderData();
        }

        // Mark for check
        this._changeDetectorRef.markForCheck();
      });

      this._checkoutService.pulseraOk$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe((pulseraOk: any) => {
        if(pulseraOk === 'error'){
          this.enableForm();
          this._checkoutService.setcheckoutOk('error');
        }

        // Mark for check
        this._changeDetectorRef.markForCheck();
      });

      this._checkoutService.checkoutOk$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe((pulseraOk: any) => {
        if(pulseraOk === 'error'){
          this.enableForm();
        }

        // Mark for check
        this._changeDetectorRef.markForCheck();
      });
  }

  /**
   * On destroy
   */
  ngOnDestroy(): void {
    // Unsubscribe from all subscriptions
    this._unsubscribeAll.next(null);
    this._unsubscribeAll.complete();
  }

  // -----------------------------------------------------------------------------------------------------
  // @ Public methods
  // -----------------------------------------------------------------------------------------------------

  onSubmit(): void {
    // console.log("object");
    this.showAlert = false;
    // this.disableButton = false;
    if (!this.signUpForm.valid) {
      this.activarClases = true;
      this.messageAlert = "Debes completar los campos del cliente para completar el registro.";
      this.viewScroller.scrollToPosition([0, 0]);
      this.showAlert = true;
      setTimeout(() => {
        this.showAlert = false;
      }, 5000);
      // this.disableButton = true;
    } else {
      if (!this.signUpForm.disabled) {
        this.activarClases = false;
        this.signUp();
      }
    }
  }

  orderData(){
    if(!this.signUpForm.valid){
      this.viewScroller.scrollToPosition([0, 0]);
      this.Toast.fire({
        icon: 'error',
        title: 'Debes completar los campos de informacion basica para continuar.'
      });
      this.signUpForm.enable();
      this._checkoutService.setuserOk('error');
      this._checkoutService.setcheckoutOk('error');
      this._checkoutService.setUserData(null);
    }else{

      // Do nothing if the form is invalid
      if (this.signUpForm.invalid) {
        return;
      }

      // Disable the form
      this.signUpForm.disable();

      let user = this.signUpForm.getRawValue();

      user.name = this.address.name;
      user.telefono = this.address.phone.replace('+52', '');
      user.direccion = this.address.address.line1;
      // user.fecha_nacimiento = this.fdnToString(user.fecha_nacimiento);

      this._checkoutService.setUserData(user);
      this._checkoutService.setuserOk('ok');
    }
  }

  enableForm(){
    setTimeout(() => {
      this.signUpForm.enable();
    }, 500);
  }

  /**
   * Sign up
   */
  signUp(): void {
    // Do nothing if the form is invalid
    if (this.signUpForm.invalid) {
      return;
    }

    // Disable the form
    this.signUpForm.disable();

    let user = this.signUpForm.getRawValue();
    user.fecha_nacimiento = this.fdnToString(user.fecha_nacimiento);
    user.fotoBase64 = this.imageBase64;
    delete user.fotoBase;

    // Sign up
    this._authService.signUp(user).subscribe(
      (response) => {
        // Navigate to the confirmation required page
        // this._router.navigateByUrl("/account-created");
      },
      (response) => {
        // Re-enable the form
        this.signUpForm.enable();
        // Set the alert
        response.error.message.forEach((element) => {
          this.messageAlert += element + ",";
        });
        // this.messageAlert = response.error.message;
        this.viewScroller.scrollToPosition([0, 0]);
        // Show the alert
        this.showAlert = true;
        setTimeout(() => {
          this.showAlert = false;
        }, 5000);
        // this.disableButton = true;
      }
    );
  }

  dateToString(fecha): any {
    var month = (fecha.getMonth() + 1).toString().padStart(2, "0");
    var day = fecha.getDate().toString().padStart(2, "0");
    var date = `${fecha.getFullYear()}-${month}-${day}`;
    return date;
  }

  fdnToString(fecha: any): any {
    return `${fecha.year}-${fecha.month.toString().padStart(2, "0")}-${fecha.day
      .toString()
      .padStart(2, "0")}`;
  }
}
