import {ChangeDetectorRef, Component, Input, OnInit} from '@angular/core';
import {WindowRef} from "../windowRef";
import {FormBuilder, FormGroup, Validators} from "@angular/forms";
import {ActivatedRoute, Router} from "@angular/router";
import Swal from 'sweetalert2';
import { environment } from 'src/environments/environment';
import { UserService } from 'src/app/core/user/user.service';
import { Subject, takeUntil } from 'rxjs';
import { CheckoutLandService } from '../checkout-land.service';
import { AuthService } from 'src/app/core/auth/auth.service';

declare global {
  interface Window {
    Stripe?: any;
  }
}

@Component({
  selector: 'app-checkout-card',
  templateUrl: './checkout-card.component.html',
  styleUrls: ['./checkout-card.component.scss']
})
export class CheckoutCardComponent implements OnInit {

  private readonly STRIPE!: any; //TODO: window.Stripe
  private elementStripe!: any;
  cardNumber: any;
  cardCvv: any;
  cardExp: any;
  form: FormGroup = new FormGroup({})
  id!: string;
  orderData!: any;
  Toast:any;

  address: any;

  private _unsubscribeAll: Subject<any> = new Subject<any>();

  
  showDatosEnvio: boolean = true;
  
  loading:boolean = false;
  
  //datos to create
  user:any = {};
  pulseras:any = [];

  paymentInfo:any = {};

  pulseraOk:any = '';
  userOk: any = '';

  checkOk: any = '';

  //usercreated
  userCreated: any = {};
  pulserasCreated: any = [];

  idTransaccion:any = '';

  metodoPago:any = '';

  datosEnvio: any;

  constructor(
    private fb: FormBuilder,
    private cd: ChangeDetectorRef, private route: ActivatedRoute,
    private _userService: UserService, private _checkoutService: CheckoutLandService,
    private _authService: AuthService, private _router: Router

  ) {
    this.STRIPE = window.Stripe(environment.stripe_pk, {locale: 'es-419'});
    this.Toast = Swal.mixin({
      toast: true,
      position: 'top-end',
      showConfirmButton: false,
      timer: 3000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener('mouseenter', Swal.stopTimer)
        toast.addEventListener('mouseleave', Swal.resumeTimer)
      }
    });
  }

  ngOnInit() {
    this.form = this.fb.group({
      // amount: ['', [Validators.required, Validators.min(1), Validators.max(100000)]],
      cardNumber: [false, [Validators.required, Validators.requiredTrue]], //TODO true | false
      cardCvv: [false, [Validators.required, Validators.requiredTrue]],//TODO true | false
      cardExp: [false, [Validators.required, Validators.requiredTrue]],//TODO true | false
    });
    this.createStripeElement();

    this._checkoutService.userData$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe((user: any) => {
        // Update the selected agente
        this.user = user;

        // Mark for check
        this.cd.markForCheck();
      });

    this._checkoutService.pulserasData$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe((pulseras: any) => {
        // Update the selected agente
        this.pulseras = pulseras;

        // Mark for check
        this.cd.markForCheck();
      });

    this._checkoutService.userOk$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe((userOk: any) => {
        // Update the selected agente
        this.userOk = userOk;
        if(this.userOk === 'ok' && this.pulseraOk === 'ok' && this.checkOk !== 'ok'){
          this._checkoutService.setcheckoutOk('ok');
        }else{
          this.enableInputs();
          this.form.enable();
        }

        // Mark for check
        this.cd.markForCheck();
      });

    this._checkoutService.pulseraOk$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe((pulseraOk: any) => {
        // Update the selected agente
        this.pulseraOk = pulseraOk;
        if(this.userOk === 'ok' && this.pulseraOk === 'ok' && this.checkOk !== 'ok'){
          this._checkoutService.setcheckoutOk('ok');
        }else{
          this.enableInputs();
          this.form.enable();
        }

        // Mark for check
        this.cd.markForCheck();
      });


    this._checkoutService.checkoutOk$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe((checkOk: any) => {
        this.checkOk = checkOk;
        if(checkOk === 'ok' && this.metodoPago === 'tarjeta'){
          this.createUser();
        }
        // Mark for check
        this.cd.markForCheck();
      });

    this._checkoutService.metodoPago$
    .pipe(takeUntil(this._unsubscribeAll))
      .subscribe((metodo: any) => {
        this.metodoPago = metodo;
        // Mark for check
        this.cd.markForCheck();
      });
  }

  private createStripeElement = () => {
    const style = {
      base: {
        color: '#000000',
        fontWeight: 500,
        fontSize: '20px',
        '::placeholder': {
          color: '#E3E2EC',
        },
      },
      invalid: {
        color: '#dc3545',
      },
    };

    //TODO: SDK de Stripe inicia la generacion de elementos
    this.elementStripe = this.STRIPE.elements({
      fonts: [
        {
          cssSrc:
            'https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400&display=swap',
        },
      ],
    });

    //TODO: SDK Construimos los inputs de tarjeta, cvc, fecha con estilos
    const cardNumber = this.elementStripe.create('cardNumber', {
      placeholder: '**** **** **** ****',
      style,
      classes: {
        base: 'input-stripe-custom'
      },
    });
    const cardExp = this.elementStripe.create('cardExpiry', {
      placeholder: 'MM/AA',
      style,
      classes: {
        base: 'input-stripe-custom'
      },
    });
    const cardCvc = this.elementStripe.create('cardCvc', {
      placeholder: '***',
      style,
      classes: {
        base: 'input-stripe-custom'
      },
    });

    const address = this.elementStripe.create('address', {
      mode: 'shipping', allowedCountries:['MX'],
      fields: {
        phone: 'always',
      },
      validation: {
        phone: {
          required: 'always',
        },
      },
    });

    //TODO: SDK Montamos los elementos en nuestros DIV identificados on el #id
    cardNumber.mount('#card');
    cardExp.mount('#exp');
    cardCvc.mount('#cvc');
    address.mount('#card-element');
    
    this.cardNumber = cardNumber;
    this.cardExp = cardExp;
    this.cardCvv = cardCvc;
    this.address = address;

    //TODO: Escuchamos los eventos del SDK
    this.cardNumber.addEventListener('change', this.onChangeCard.bind(this));
    this.cardExp.addEventListener('change', this.onChangeExp.bind(this));
    this.cardCvv.addEventListener('change', this.onChangeCvv.bind(this));
  }

  async initPay(){
    let address = this.elementStripe.getElement('address');
    const {complete, value} = await address.getValue();

    if(complete){
      this.datosEnvio = value;
      this.orderDataCliente(value);
    }
  }

  orderDataCliente(address:any){
    console.log("aqui");
    this._checkoutService.setProcessing('loading');
    this._checkoutService.setAddressData(address);
  }

  async executePayment(id:any): Promise<any>{
    try {
      //TODO: SDK de Stripe genera un TOKEN para la intencion de pago!
      const {token} = await this.STRIPE.createToken(this.cardNumber);
  
      //TODO: Enviamos el token a nuesta api donde generamos (stripe) un metodo de pago basado en el token
      //TODO: tok_23213
      const data = await this._checkoutService.sendPayment(token.id, this.paymentInfo._id);
      this.idTransaccion = data.payment_method;
      // this._checkoutService.setIdTransaccion(data.payment_method);
      //TODO: Nuestra api devolver un "client_secret" que es un token unico por intencion de pago
        //TODO: SDK de stripe se encarga de verificar si el banco necesita autorizar o no
        this.STRIPE.handleCardPayment(data.client_secret)
          .then(async () => {
  
            //TODO: 👌 Money Money!!!
            this.Toast.fire({
              icon: 'success',
              title: 'Pago realizado con exito'
            });
  
            //TODO: Enviamos el id "localizador" de nuestra orden para decirle al backend que confirme con stripe si es verdad!
            await this._checkoutService.confirmPayment(this.paymentInfo._id);
            // this.setAddress(value);
            var redirectURL:any;
            redirectURL = `/checkout-exito/${id}`;
            // Navigate to the redirect url
            this._router.navigateByUrl(redirectURL);
          })
          .catch(() => {
            this.showDatosEnvio = true;
            this.enableInputs();
            this.Toast.fire({
              icon: 'error',
              title: 'Error en el pago'
            });
            this.form.enable();
            this._checkoutService.setProcessing('error');
          })

    } catch (error) {
      this.showDatosEnvio = true;
      this.enableInputs();
      this.form.enable();
      this.Toast.fire({
        icon: "error",
        title: "Algo ocurrio mientras se procesaba el pago",
      });
      this._checkoutService.setProcessing("error");
    }
  }

  setAddress(data:any):void{
    this._checkoutService.setAddessPulsera({otros: data}, this.paymentInfo.manillaId).pipe(takeUntil(this._unsubscribeAll)).subscribe(
      (response:any) => {
        this.Toast.fire({
          icon: 'success',
          title: 'La direccion de entrega de la pulsera ha sido agregada con exito.'
        });
        // this._checkoutService.setStatus('success');
        // this._checkoutService.setAddress(data);
      },(error) => {
        this.Toast.fire({
          icon: 'error',
          title: error.error.message
        });
      }
    );
  }

  disableInputs(){
    this.loading = true;
    this.cardNumber.update({disabled:true});
    this.cardExp.update({disabled:true});
    this.cardCvv.update({disabled:true});
  }

  enableInputs(){
    this.loading = false;
    this.cardNumber.update({disabled:false});
    this.cardExp.update({disabled:false});
    this.cardCvv.update({disabled:false});
  }

  //TODO: Manejadores de validacion de input de stripe

  onChangeCard({error}: any) {
    this.form.patchValue({cardNumber: !error});
  }

  onChangeCvv({error}: any) {
    this.form.patchValue({cardCvv: !error});
  }

  onChangeExp({error}: any) {
    this.form.patchValue({cardExp: !error});
  }

  //Creacion usuario
  createUser(){
    setTimeout(() => {
      this.disableInputs();
      this.form.disable();
      this.verificarUser();
    }, 200);
  }

  /**
   * Sign up
   */
  signUp(): void {

    // Sign up
    this._authService.signUp(this.user).subscribe(
      (response) => {
        this.userCreated = response;
        let data = {
          email: this.user.email,
          password: this.user.password
        }

        this.signIn(data);
      },
      (response) => {
        // Re-enable the form
        this._checkoutService.setuserOk('error');
        this._checkoutService.setcheckoutOk('error');
        this._checkoutService.setpulseraOk('error');
        let message:any = response.error.message;
        // Set the alert

        this.Toast.fire({
          icon: 'error',
          title: message
        });
        // this.disableButton = true;
      }
    );
  }

  signIn(data:any): void{
    // Sign in
    this._authService.signIn(data).pipe(takeUntil(this._unsubscribeAll))
    .subscribe(
      (response) => {
        this.userCreated = response.user;
        this.ciclarPulseras(response.user);
      },
      (response) => {
        // Re-enable the form
        this._checkoutService.setuserOk('error');
        this._checkoutService.setcheckoutOk('error');
        this._checkoutService.setpulseraOk('error');
        let message:any = response.error.message;
        // Set the alert

        this.Toast.fire({
          icon: 'error',
          title: message
        });
      }
    );
  }

  verificarUser(){
    this._authService.existUser(this.user.email).pipe(takeUntil(this._unsubscribeAll)).subscribe(
      (response:any) => {
        if(response){
          let data = {
            email: this.user.email,
            password: this.user.password
          }
  
          this.signIn(data);
        }else{
          this.signUp();
        }
      },(error) => {
    
      }
    );
  }

  ciclarPulseras(user:any){
    this.pulseras.pulseras.forEach(element => {
      element.userId = user._id;
      element.otros = this.datosEnvio;
    });

    this.crearPulseras(this.pulseras.pulseras);
  }

  crearPulseras(data:any){
    this._userService.crearVariasManilla({manillas: data}).subscribe(
      (response) => {
        this.pulserasCreated = response;
        this.createPaymentIntent();
      },
      (response) => {
        this.Toast.fire({
          icon: 'error',
          title: response.error.message
        });
      }
    );
  }

  createPaymentIntent():any{
    let data = {
      userId: this.userCreated._id,
      monto: this.pulseras.precio,
      metodo: 'Stripe',
      manillasId: this.getPulserasIds(),
      otros:{
        list: this.pulseras.lista,
        precio: this.pulseras.precio,
        envio: this.datosEnvio
      }
    }

    this._checkoutService.generateOrder(data).pipe(takeUntil(this._unsubscribeAll)).subscribe(
      (response:any) => {
        this.paymentInfo = response;
        this.executePayment(response._id);
      },(error) => {
        this.Toast.fire({
          icon: 'error',
          title: error.error.message
        });
      }
    );
  }

  getPulserasIds(){
    let pulseras: any = [];
    this.pulserasCreated.forEach(pulsera => {
      pulseras.push(pulsera._id);
    });

    return pulseras;
  }

}
